@import "helpers/variables";
@import "base/fonts";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/general";
@import "../blocks/modules";

/* sing-in */


.content-sign-in {
  padding: 0;
  grid-column: 10 / 16; }

.header-1 {
  grid-row: 1;
  grid-column: 12 / 14;
  height: 75px;
  display: flex;
  align-items: center;
  justify-content: space-around; }

.header-1-img-logo {
  align-self: center;
  width: 100%; }


.a-div-close {
  padding: 30px;
  grid-row: 1;
  grid-column: 24;
  justify-content: flex-start;
  padding-left: 0; }

.div-sign-in {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }


.div-form-sign-in {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }


.h3-sign-in {
  text-align: center;
  margin-bottom: 15px; }

.div-form-sign-in {
  color: $text-light;
  display: flex;
  justify-content: space-between;
  min-height: 400px; }

.label-sign-in {
  align-self: flex-start;
  width: 100%;
  display: inline-block;
  padding: 10px 0; }

.input-sign-in {
  width: 100%;
  height: 45px;
  border: 1px solid $outline;
  border-radius: 5px; }

.div-button-sign-in {
  width: 100%; }

.button-sign-in {
  cursor: pointer;
  height: 65px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 10px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  &:hover {
    background-color: #276CDE; } }

.button-disabled-sign-in {
  background-color: $back-dark;
  color: $text-light; }

.button-default-sign-in {
  background-color: $blue-1;
  color: #fff; }

.a-sign-in {
  text-decoration: none;
  color: $text-light;
  line-height: 25px; }

/* register */

.new-user {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px 0 0 0; }

.p-new-user {
  margin: 0;
  padding: 0;
  line-height: 17px;
  color: $text-dark; }

.a-new-user {
  &:link, &:visited {
    color: $blue-1; }
  &:active, &:hover {
    color: $text-dark; } }

.div-form-register {
  color: $text-light;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  min-height: 500px; }

.div-personal-data-register {
  margin: 10px 0; }

.personal-data-register {
  color: $text-dark;
  line-height: 14px; }

.a-personal-data-register {
  color: $text-dark; }

.content-register {
  padding: 0;
  grid-column: 10 / 16; }

/* catalog */

.catalog-content {
  grid-column: 1 / 25;
  align-self: flex-start;
  padding: 0 6rem; }

.catalog-content-h2 {
  padding: 10px 0 30px 0; }

.catalog-content-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: repeat(auto, 410px);
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.catalog-card {
  background-color: #fff;
  border-radius: 13px;
  height: 100%;
  box-shadow: 0 3px 10px rgba(23, 50, 104, 0.1);
  display: grid;
  grid-template-rows: auto auto; }

.catalog-card-img {
  width: 100%;
  position: relative;
  border-radius: 13px 13px 0 0; }

.bought-img-mask {
  position: absolute;
  width: 100.1%;
  left: -0.413px;
  top: 0; }

.catalog-card-desc {
  padding: 28px 15px;
  display: grid;
  grid-template-rows: 1fr 65px; }

.catalog-card-desc-text {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.catalog-card-duration {
  justify-content: flex-end; }

.catalog-card-desc-h3 {
  padding-bottom: 10px; }

.catalog-card-desc-p {
  color: $text-light;
  margin-bottom: 40px; }

.catalog-card-a {
  text-decoration: none; }

.catalog-card-a-button {
  cursor: pointer;
  height: 65px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 10px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  background-color: $blue-1;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  &:hover {
    background-color: #276CDE; } }

.catalog-card-a-button-arrow {
  margin-left: 10px; }

.catalog-card-info {
  border-radius: 10px;
  height: 100%;
  width: 100%;
  background-color: #E3F2FD;
  display: flex;
  align-content: center;
  align-items: center;
  padding: 0 1.5rem; }


.catalog-card-info-container {
  display: flex;
  align-items: flex-start;
  width: 100%; }

.catalog-card-info-p {
  margin: 0 0 0 16px;
  color: $text-light; }

.catalog-card-open_access_1-p {
  color: $text-light;
  margin: 16px 0 5px 0; }

.catalog-card-open_access_2-p {
  color: $blue-1;
  margin: 0; }

.catalog-card-open_access-a-button {
  cursor: pointer;
  height: 45px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 10px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  background-color: #fff;
  color: #3B7BF6;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  border: 1px solid $back-dark;
  &:hover {
    background-color: #F8F8F7;
    color: $text-dark; } }

.catalog-card-open_access_2-p {
  margin-bottom: 100px; }

@media screen and (max-width: $max-mob-width) {
  .catalog-card-desc {
    padding: 26px; }

  .catalog-card-info {
    padding: 26px; }

  .catalog-card-info-img {
    margin-right: 19px; }

  .catalog-card-desc-h3 {
    padding-bottom: 4px; }

  .catalog-card-desc-p {
    margin-bottom: 10px; }

  .catalog-card-info-p {
    margin: 0 0 0 10px;
    line-height: 20px; }

  .catalog-card-duration {
    font-size: 11px; } }

@media screen and (max-width: 1450px) {
  .catalog-content {
    padding: 0 5rem; } }

@media screen and (max-width: 1150px) {
  .catalog-content {
    padding: 0 3rem; } }

/* blank_my_course */

.my_course-content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column: 1 / 25;
  align-self: flex-start;
  padding: 0 6rem; }

.my_course-content-h2 {
  grid-column: 1 / 13; }

.buy_course_block {
  display: grid;
  grid-template: 2fr 0.5fr 0.5fr 2fr / repeat(12, 1fr);
  grid-column: 4 / 10;
  background-color: #E3F2FD;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  margin: 50px 0 150px 0; }


.buy_course_block-a {
  text-decoration: none;
  color: #fff;
  grid-row: 3;
  grid-column: 5 / 9; }

.buy_course_block-p {
  grid-row: 2;
  grid-column: 3 / 11;
  text-align: center;
  color: $text-light; }


.buy_course_block-a-button {
  cursor: pointer;
  height: 45px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 5px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  background-color: #fff;
  color: #3B7BF6;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  &:hover {
    background-color: #F8F8F7;
    color: $text-dark; } }

@media screen and (max-width: 1450px) {
  .my_course-content {
    padding: 0 5rem; } }

@media screen and (max-width: 1150px) {
  .my_course-content {
    padding: 0 3rem; } }


.catalog-card-progress_bar-p {
  text-align: right;
  color: $blue-1; }

.catalog-card-progress_bar-empty {
  width: 100%;
  height: 6.5px;
  background-color: $back-dark;
  border-radius: 5px; }

.catalog-card-progress_bar {
  width: 14%;
  height: 6.5px;
  background-color: $blue-1;
  border-radius: 5px; }

.catalog-card-a-button {
  cursor: pointer;
  height: 45px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 10px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  background-color: $blue-1;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  &:hover {
    background-color: #276CDE; } }


.back_arrow {
  padding: 13px 18px;
  display: inline-block;
  &:hover {
    background-color: $back-dark;
    border-radius: 6px; } }

.current_course-head {
  display: flex;
  align-items: center;
  margin: 0 0 52px 0; }

.current_course-h2 {
  display: inline-block;
  margin: 0 40px; }

.current_course-author {
  display: inline-block;
  color: $text-light; }

.current_course-cards {
  display: grid;
  grid-column-gap: 1.7rem;
  grid-row-gap: 2rem;
  grid-template-columns: repeat(auto-fill, 75px);
  justify-content: center; }

.current_course-card {
  width: 100%; }

.current_course-card-img {
  width: 100%; }

.current_course-card-info {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.current_course-card-info-percent {
  color: $blue-1; }

.current_course-card-info-duration {
  color: $text-light; }

.current_course-card-progress {
  width: 100%;
  height: 6.5px;
  background-color: $back-dark;
  border-radius: 5px; }

.current_course-card-progress_bar {
  width: 14%;
  height: 6.5px;
  background-color: $blue-1;
  border-radius: 5px; }

.current_course-card-img {
  padding: 25px 25px 21px 25px;
  cursor: pointer;
  border-radius: 10px;
  background-color: $blue-1;
  &:hover {
    background-color: #276CDE; } }

.current_course-card-done {
  position: relative;
  .current_course-card-img {
    background-color: #E3F2FD;
    &:hover {
      background-color: #D8E7F1; } } }

.current_course-card-done-img {
  position: absolute;
  right: -9px;
  top: -9px; }

.current_course-card-locked {
  .current_course-card-img {
    background-color: $back-dark;
    &:hover {
      background-color: $back-dark; } }

  .current_course-card-info {
    opacity: 0.35; }

  .current_course-card-progress {
    opacity: 0.35; } }

/*     edit-form     */

.edit-form {
  grid-column: 7 / 19;
  align-items: center;
  justify-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 2rem;
  color: $text-light;
  font-size: 14px;
  margin-top: 50px; }

.edit-form-profile_img {
  grid-column: 1 / 5;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 67px;
  margin-bottom: 30px; }

.edit-form-profile_img-plus {
  margin: 0 12px;
  height: 20px; }

.edit-form-profile_img-img {
  height: 70px; }

.upload-photo {
  &:link, &:visited {
    color: $blue-1; }
  &:active, &:hover {
    color: $text-dark;
    text-decoration: none; } }

.name-edit, .phone-edit {
  grid-column: 1 / 3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 18px; }

.family_name-edit, .city-edit {
  grid-column: 3 / 5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 18px; }

.password-edit {
  grid-column: 1 / 5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 18px;
  position: relative; }

.password-control {
	position: absolute;
	top: 26px;
	right: 0;
	display: inline-block;
	width: 40px;
	height: 40px;
	background: url(../img/password-control.png); }

.edit-button {
  grid-column: 2 / 4;
  cursor: pointer;
  height: 65px;
  width: 100%;
  padding: 0;
  align-self: center;
  border: none;
  border-radius: 10px;
  outline: none;
  text-align: center;
  margin: 12px 0;
  font-weight: 600;
  background-color: $blue-1;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  font-size: 16px;
  &:hover {
    background-color: #276CDE; } }

.name-edit-label, .family_name-edit-label, .phone-edit-label, .city-edit-label, .password-edit-label {
  margin-bottom: 8px; }

.name-edit-input, .family_name-edit-input, .phone-edit-input, .city-edit-input, .password-edit-input {
  height: 45px;
  border-radius: 5px;
  border: 1px solid #CECECE;
  padding: 12px 14px; }

.sign-out-a {
  grid-column: 12 / 14;
  text-align: center;
  &:link, &:visited {
    color: $blue-1; }
  &:active, &:hover {
    color: $text-dark;
    text-decoration: none; } }

.delete-profile {
  grid-column: 1 / 25;
  border-top: 1px solid #E2E8ED;
  padding-top: 34px;
  text-align: center; }

.delete-profile-a {
  &:link, &:visited {
    color: $text-light; }
  &:active, &:hover {
    color: $text-dark;
    text-decoration: none; } }

.edit_form-close {
  display: none; }

.div-personal-data-register {
  margin-top: 25px; }

.personal-data-register {
  display: inline-grid;
  line-height: 18px; }

@media screen and (max-width: $max-mob-width) {
  .content-sign-in {
    padding: 0;
    grid-column: 2 / 5; }

  .header-1 {
    grid-column: 3;
    height: 75px;
    width: 100%;
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: space-around; }

  .a-div-close {
    padding: 20px 0 20px 20px;
    grid-row: 1;
    grid-column: 4;
    margin: 0 0 0 auto;
    justify-content: flex-start; }

  .div-form-sign-in {
    min-height: 100px; }

  .new-user {
    margin: 100px 0; }

  .label-sign-in {
    padding: 25px 0 10px 0;
    font-weight: 300; }

  .input-sign-in {
    width: 100%;
    height: 45px;
    border-radius: 5px;
    border-color: $outline;
    border-style: solid; }

  .button-default-sign-in {
    margin-top: 40px; }


  .p-new-user {
    padding-bottom: 10px; }

  .content-register {
    padding: 0;
    grid-column: 2 / 5; }

  .edit-form {
    grid-column: 2 / 5; }

  .edit-form-profile_img {
    grid-column: 1 / 5; }

  .sign-out-a {
    grid-column: 3; }

  .delete-profile {
    grid-column: 1 / 6; }

  .edit_form-close {
    display: inline-block;
    position: absolute;
    top: 20px;
    right: 20px; }

  .name-edit, .phone-edit {
    grid-column: 1 / 5; }

  .family_name-edit, .city-edit {
    grid-column: 1 / 5; }

  .password-edit {
    grid-column: 1 / 5; }

  .password-control {
    display: none; }

  .edit-button {
    grid-column: 1 / 5; }

  .delete-profile {
    padding: 34px 0; }

  .catalog-content {
    grid-column: 1 / 6;
    padding: 0 1.5rem; }

  .catalog-content-cards {
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: repeat(auto, 410px); }

  .catalog-card-open_access_2-p {
    margin-bottom: 50px; }

  .my_course-content {
    grid-column: 2 / 5;
    padding: 0; }

  .buy_course_block {
    grid-template-rows: 40px 1fr 1fr 40px;
    grid-column: 1 / 13;
    height: 200px;
    margin-top: 22px; }

  .buy_course_block-a {
    grid-column: 3 / 11; }

  .buy_course_block-p {
    grid-column: 2 / 12; }

  .current_course-head {
    display: grid;
    grid-template-columns: 45px 1fr; }

  .back_arrow {
    grid-column: 1; }

  .current_course-h2, .current_course-author {
    grid-column: 2;
    margin: 0; }

  .current_course-cards {
    margin-bottom: 50px; }

  .bought-img-mask {
    position: absolute;
    width: 100.1%;
    width: 100.4%;
    left: -0.4125px;
    top: -0.4125px; } }

@media screen and (max-width: 450px) {
  .catalog-content-cards {
    grid-template-columns: 1fr; }

  .catalog-card-info {
    min-height: 410px; }

  .w300 {
    font-weight: 300; } }
