.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: $back-dark;
  width: 100%;
  height: 245px;
  grid-column: 1 / 25;
}

.body-sign-in > .footer {
    grid-row: 3;
}

.contact-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #2979FF;
  font-weight: 600;

}
.phone-contact-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $back-light;
  padding: 13px 60px;
  height: 45px;
  margin: 10px 0;
  min-width: 227.73px;
  border-radius: 5px;
  &:hover {
    color: $text-dark;
  }
}

.wa-contact-footer {
  border-radius: 7px;
}

.a-wa-contact-footer {
  margin: 10px;
  position: relative;
  &:hover .wa_mask {
    opacity: .15;
    transform: translateY(0%);
  }
  &:active  {
    width: 35px;
    height: 35px;
    margin: 15px;
    &> img {
      width: 35px;
      height: 35px;
    }
  }
}

.wa_mask {
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  right:0;
  opacity: 0;
  height: 100%;
  border-radius: 7px;
  background: #000;
  transform: translateY(100%);
}

.a-tg-contact-footer {
  position: relative;
  &:hover .wa_mask {
    opacity: .15;
    transform: translateY(0%);
  }
  &:active  {
    width: 35px;
    height: 35px;
    margin: 5px;
    &> img {
      width: 35px;
      height: 35px;
    }
  }
}

.tg-contact-footer {
  border-radius: 7px;
}

.p-copyright-footer {
  text-align: center;
}

.p-copyright-footer {
  color: $text-light;
  margin: 0;
}

.ul-copyright-footer{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 400px;
  &> li {
    list-style: none;
    padding: 10px 0;
    line-height: 14px;
    font-size: 14px;
    color: $text-dark;
    &> a {
      &:active, &:link, &:visited, &:hover {
      color: $text-dark;
      }
    }
  }
}

@media screen and (max-width: $max-mob-width) {
  .footer {
    grid-column: 1 / 6;
  }

  .contact-footer {
    width: 100%;
  }

  .p-phone-contact-footer {
    font-size: 14px;
  }

  .phone-contact-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $back-light;
    padding: 13px 20px;
    height: 45px;
    margin: 10px 0;
    min-width: 220.73px;
    border-radius: 5px;
    &:hover {
      color: $text-dark;
    }
  }

  .body-sign-in {
    .footer {
      display: none;
    }
  }

  .body-edit-form {
    .footer {
      display: none;
    }
  }

  .question-footer {
    width: 100%;
    padding: 0 10px;
    text-align: center;
  }
}
@media screen and (max-width: 450px) {
  .phone-contact-footer {
    min-width: 60%;
    }

  .ul-copyright-footer {
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    &> li {
      list-style: none;
      padding: 7px 0;
      color: $text-dark;
      height: 100%;
    }
    & li:nth-child(4n)  {
      display: none
    }
  }

  .author-ul-copyright-footer {
    width: 80%;
    text-align: center;
  }
  .wa-contact-footer {
    box-shadow: none;
  }
  .tg-contact-footer {
    box-shadow: none;
  }

  .phone-contact-footer {
    box-shadow: none;
  }
}
