.header {
    grid-column: 1 / 25;
    grid-row: 1;
    display: grid;
    grid-template-columns: 3rem 2fr 4fr 10fr 4fr 2fr 3rem;
    grid-template-rows: 25px 25px 25px;
    grid-column-gap: 3rem;
    align-items: center;
    align-self: flex-start;
    background: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}

.header-logo {
  height: 100%;
  grid-column: 2;
  grid-row: 2;
}

.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-column: 4;
  grid-row: 1 / 4;
  text-align: center;
  height: 100%;
}

.header-menu-li {
  height: 100%;
  display: inline-block;
  padding: 25px 20px;
  &:hover {
    border-bottom: 2px solid $blue-1;
  }
}
.header-menu-a {
  text-decoration: none;
  &:link, &:visited, &:hover, &:active {
    color: $text-dark;
  }
}


.header-menu-li-active {
  border-bottom: 2px solid $blue-1;
  .header-menu-a:link, .header-menu-a:visited, .header-menu-a:hover, .header-menu-a:active {
    color: $blue-1;
  }
}

.header-profile {
  display: flex;
  padding: 12px 0;
  height: 76px;
  grid-column: 6;
  grid-row: 1 / 4;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  &:hover {
    border-bottom: 2px solid $blue-1;
  }
}

.header-profile-name {
  color: $text-dark;
  line-height: 17px;
  margin: 0 10px
}

.header-profile-city {
  color: $text-light;
  margin: 5px 10px;
}

.header-toggle-menu {
  display: none;
}

.mask-header-mobile-menu {
  display: none;
}

@media screen and (max-width: 1450px) {
  .header {
    grid-template-columns: 2rem 2fr 1fr 10fr 1fr 2fr 2rem;
  }
}

@media screen and (max-width: 1150px) {
  .header {
    grid-template-columns: 2rem 2fr 0 10fr 0 2fr 2rem;
    grid-column-gap: 1rem;
  }
}

@media screen and (max-width: $max-mob-width) {
  .header {
    grid-column: 1 / 6;
    grid-template-columns: 1rem 2fr 2fr 1rem;
    background-color: $back-dark;
  }

  .header-toggle-menu {
    display: inline-block;
    grid-column: 3;
    grid-column-gap: 1rem;
    grid-row: 2;
    margin: 0 0 0 auto;
  }

  .header-menu {
    display: none;
  }

  .header-profile {
    display: none;
  }

  .body-edit-form {
    .header {
      display: none;
    }
  }

  .mask-header-mobile-menu {
    display: none; //inline-block
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100rem;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 998
  }

  .header-mobile-menu {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    grid-column: 1 / 4;
    width: 75%;
    height: 600px;
    display: flex;
    flex-direction: column;
    z-index: 999;
    padding: 10px 7px;
    .header-mobile-profile {
      border-bottom: 1px solid #E2E8ED;
    }

    .header-menu-li {
      height: auto;
      display: inline-block;
      padding: 25px 20px;
      border-bottom: 1px solid #E2E8ED;
    }

    .header-menu-li-active {
      border-bottom: 1px solid #E2E8ED;
    }

    .header-mobile-menu-logout {
      margin: auto 0 0 0;
      border-bottom: none;
      .header-menu-a {
        display: flex;
        align-items: center;
        .logout-img {
          margin-right: 20px;
        }
      }
    }
  }

  .header-menu-a {
    display: flex;
    align-items: center;
  }

  .myCatalog, .myCourse, .myTask {
    margin-right: 20px;
  }

  .myCatalog-active, .myCourse-active, .myTask-active {
    display: none;
  }

  .header-mobile-profile {
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 15px 0;
    .header-profile-name {
      font-size: 20px;
      line-height: 22px;
    }
    .header-profile-city {
      font-size: 16px;
    }
    .header-profile-img {
      width: 67px;
    }
  }

  .close-header-mobile {
    position: absolute;
    top: 18px;
    right: 33px;
    z-index: 999;
  }
}
