h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(690));
    @include lg(max-width, rem(930));
    @include xl(max-width, rem(1110));
}

input[type=text] {
    /* Remove First */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

html {
  background-color: $back-light;
  font-family: 'Open Sans', sans-serif;
  height: 100%;
}

body {
  display: grid;
  grid-template-columns: repeat(24, 1fr);
  grid-template-rows: 71px 2fr 235px;
  grid-row-gap: 43px;
  grid-column-gap: 1rem;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.body-sign-in {
  grid-row-gap: 56px;
}

* {
  outline-color: $outline;
  box-sizing: border-box;
}

.h1-sb {
  font-size: 42px;
  font-weight: 600;
}

.h2-sb {
  font-size: 30px;
  font-weight: 600;
}

.h3-sb {
  font-size: 20px;
  font-weight: 600;
}

.text-reg-1 {
  font-size: 16px;
  font-weight: 400;
}

.text-reg-2 {
  font-size: 14px;
  font-weight: 400;
}

.text-reg-3 {
  font-size: 11px;
  font-weight: 400;
}

@media screen and (max-width: $max-mob-width) {
  body {
    grid-template-columns: 20px 1fr 1fr 1fr 20px;
  }

  .h1-sb {
    font-size: 22px;
  }
  .h2-sb {
    font-size: 20px;
  }
  .h3-sb {
    font-size: 20px;
  }
}

@media screen and (max-width: 450px) {
  body {
    grid-template-columns: 1rem 0.7fr 1.6fr 0.7fr 1rem;
    grid-row-gap: 45px
  }
}
